.profileImage{
    width: 200px;
    height: 200px;
}
img{
    margin: auto;
    width: 100%;
    height: auto;
}


.userWrapper{
    display: block;
    margin-left: 50px;
    margin-top: 10px;
    border: 3px solid cornflowerblue;
    border-radius: 4px;
    width: 200px;
    overflow: hidden;
}
.pagesIcon{
    margin-right: 5px;
    border: 1px solid cornflowerblue;
    padding: 1px 5px;
}
.activePage{
    border: 3px solid black;
    padding: 1px 5px;
    font-weight: 8px;
}
.findUsersWrapper{
    border: 2px solid #9CBBF4;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    width: 100%;
}
